@import './styles/common.scss';

// normalize

html {
  // scroll-behavior: smooth;
}

body {
  font-family: 'Montserrat', sans-serif;
  background-color: $bg-color;
  color: $primary-color;
  opacity: 0;
  overscroll-behavior-y: none;
}

ul {
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  cursor: pointer;
}

h1, h2, h3 {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

// loading screen

.loading-screen {
  width: 100vw;
  height: 0;
  background-color: rgb(22, 22, 21);
  position: fixed;
  z-index: 10;
}

// container

.container {
  width: 1200px;
  position: relative;
  margin: auto;

  @media (max-width: 500px) {
    width: 100%;
  }
}

.App {
  overflow: hidden;
}

.playground-container {
  position: relative;
  overflow: hidden;
  overflow: hidden;
  width: 100vw;
  height: 100vh;

  a {
    position: relative;
    z-index: 3;
  }

  h1 {
    z-index: 1;
    font-family: 'Bebas Neue', sans-serif;
    font-size: 20vw;
    position: absolute;
    color: #fff;
    left: 50%;
    top: 0%;
    transform: translateX(-50%);
    width: 100%;
    text-align: center;
  }

  canvas {
    width: 100vw;
    height: 100vh;
    z-index: 2;
    position: fixed;
    right: 0px;
    bottom: 0px;
    top: 0px;
    left: 0px;
  }
}