@import './common.scss';

.menu-container {
  position: fixed;
  z-index: 10;
  
  .menu {
    height: 100vh;
    width: 30vw;
    left: -30vw;
    padding: 100px;
    box-sizing: border-box;
    position: absolute;
  
    background-color: #fff;
    height: 100vh;
    
    z-index: 5;
  
    transition: 0.3s;
  
    ul li {
      margin-bottom: 10px;
    }
  
    ul li a {
      padding: 10px;
      display: inline-block;
      color: $primary-color;
      font-size: $font-med;
      transition: 0.3s;
  
      &:hover {
        color: #FC6767;
      }
    }
  
    &.open {
      left: 0px;
    }
  }
  
  .menu-button {
    top: 20px;
    left: 20px;
    position: fixed;
    z-index: 10;
    cursor: pointer;
    padding: 23px 20px;
    background-color: #000;
    border-radius: 50px;

    transition: transform 0.2s;

    &:hover {
      transform: scale(1.1);
    }

    &:active {
      transform: translateY(2px);
    }

    &.active {

      span {
        opacity: 1;
        transform: rotate(45deg) translate(3px, -6px);
        background: #ffffff;

        &:nth-last-child(2) {
          opacity: 0;
          transform: rotate(0deg) scale(0.2, 0.2);
        }

        &:nth-last-child(1) {
          transform: rotate(-45deg) translate(1px, 1px);
        }
      }
    }

    span {
      display: block;
      width: 33px;
      height: 4px;
      margin-bottom: 5px;
      position: relative;
      
      background: #ffffff;
      border-radius: 3px;
      
      z-index: 1;
      
      transform-origin: 4px 0px;
      
      transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                  opacity 0.55s ease;

      &:first-child {
        transform-origin: 0% 0%;
      }

      &:nth-last-child(2) {
        transform-origin: 0% 100%;
      }
    }
  }
}

