@import '../common.scss';

.antimatter-container {
  background: #C6FFDD;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #f7797d, #FBD786, #C6FFDD);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #f7797d, #FBD786, #C6FFDD); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  position: relative;
  width: 100%;
  overflow: hidden;
  height: 100%;

  h1 {
    color: #666757;
    opacity: .5;
    mix-blend-mode: color-dodge;
    height: 20vw;
    overflow: hidden;

    span {
      position: relative;
      display: inline-block;
    }
  }

  ul.options {
    list-style: none;
    padding: 0;
    display: flex;
    gap: 20px;
    font-family: 'Bebas Neue', sans-serif;
    font-size: 32px;
    /* padding: 40px; */
    top: 150px;
    z-index: 4;
    position: relative;
    justify-content: center;
  }
  
  ul.options li {
    padding: 10px;
    cursor: pointer;
    color: #fff;
    transition: transform 0.3s;
  }
  
  ul.options li:hover {
    transform: scale(1.4);
  }
  
  ul.options li.selected.glow {
    color: #ff661b;
  }
  
  ul.options li.selected.mars {
    color: #ffa9a9;
  }
  
  ul.options li.selected.breeze {
    color: #009fff;
  }
  
  ul.options li.selected.fire {
    color: #ff3030;
  }
  
  ul.options li.selected.jealousy {
    color: #32a97e;
  }
}
