.close-btn {
  // padding: 40px;
  z-index: 10;
  top: 60px;
  right: 60px;
  position: fixed;
  height: 60px;
  width: 60px;
  background: none;
  cursor: pointer;
  border: none;
  transition: 0.2s;
  border-radius: 50px;
  padding: 20px;
  display: block;


  &:before, &:after {
    position: absolute;
    left: 28px;
    top: 5px;
    content: ' ';
    height: 50px;
    width: 3px;
    border-radius: 3px;
    background-color: #333;
    transition: 0.3s;
  }
  &:before {
    transform: rotate(45deg);
  }
  &:after {
    transform: rotate(-45deg);
  }

  &:hover {
    background-color: #000;
    transform: scale(1.3);

    &:before {
      transform: scale(0.6) rotate(45deg);
      background-color: #fff;
    } 

    &:after {
      transform: scale(0.6) rotate(-45deg);
      background-color: #fff;
    }
    
  }
}

.content-item {

